<template>
  <div class="home">
    <Panel width="80%">
      <template #header>
        <div class="nav">
          <router-link
            to="/"
            :class="{ 'router-link-exact-active': isHomeActive }"
            >全部</router-link
          >
          <router-link to="/good">精华</router-link>
          <router-link to="/share">分享</router-link>
          <router-link to="/ask">问答</router-link>
          <router-link to="/job">招聘</router-link>
          <router-link to="/dev">客户端测试</router-link>
        </div>
      </template>
      <template #content>
        <div v-if="posts.length" class="content">
          <div v-for="post in posts" :key="post.id" class="post-item">
            <img
              :src="post.author.avatar_url"
              :title="post.author.loginname"
              alt=""
            />
            <span class="count">
              <span> {{ post.reply_count }} </span>
              /
              <span> {{ post.visit_count }} </span>
            </span>
            <span>
              <span
                v-if="!tab || post.good || post.top"
                :class="post.good || post.top ? 'tab-active' : 'tab'"
                >{{ post | getTapByEnglish }}</span
              >
            </span>
            <router-link :to="post.id"> {{ post.title }} </router-link>
            <span class="last-reply">
              {{
                $moment(`${post.last_reply_at}`)
                  .startOf("minute")
                  .fromNow()
              }}
            </span>
          </div>
          <div>
            <el-pagination
              :current-page="currentPage"
              :page-size="40"
              background
              layout="prev, pager, next"
              :total="400"
              @current-change="pageChange"
            >
            </el-pagination>
          </div>
        </div>
      </template>
    </Panel>
  </div>
</template>

<script>
import Panel from "../layout/Panel.vue";
// @ is an alias to /src

export default {
  name: "Home",
  components: { Panel },
  data() {
    return {
      posts: [],
      pageTotalList: [
        {
          tab: "all",
          total: 360
        }
      ],
      currentPage: 1
    };
  },
  // 监听动态路由参数获取分类
  watch: {
    tab: {
      async handler(newValue) {
        const queryTab = newValue ? `&tab=${newValue}` : "";
        const res = await this.$axios.get(
          `./topics?page=1&limit=360${queryTab}`
        );
        this.posts = res.data;
        // console.log(this.posts);
      },
      immediate: true
    }
  },
  computed: {
    tab() {
      return this.$route.params.tab;
    },

    isHomeActive() {
      return ["/"].includes(this.$route.path);
    },
    showPosts() {
      return this.posts.slice(
        (this.currentPage - 1) * 40,
        this.currentPage * 40
      );
    }
  },
  methods: {
    pageChange(page) {
      this.currentPage = page;
      console.log(this.currentPage);
      // const res = this.$axios.get(`./topics?page=${this.currentPage}&limit=40`);
      // this.posts = res.data;
    }
  }
  // async created() {
  //   const res = await this.$axios.get("./topics?page=1&limit=40");
  //   this.posts = res.data;
  //   console.log(this.posts);
  // }
};
</script>
