<template>
  <div class="panel" :style="{ width: formatWidth }">
    <div class="header">
      <slot name="header" />
    </div>
    <div class="content">
      <slot name="content" />
    </div>
  </div>
</template>

<script>
export default {
  props: {
    width: {
      type: [Number, String],
      default: "100%"
    }
  },
  computed: {
    formatWidth() {
      return typeof this.width === "number" || Number(this.width)
        ? this.width + "px"
        : this.width;
    }
  }
};
</script>

<style lang="less" scoped>
.panel {
  width: 100%;
  margin: 10px 0;
  .header {
    padding: 10px;
    background-color: #f6f6f6;
    border-radius: 3px 3px 0 0;
  }
}
</style>
